/* ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Î∞î ÏÉÅÎã® Í≥†Ï†ï */
.navbar {
  width: 100%;
  height: 70px;
  background-color: #1a1a1a;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
}

/* ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò ÏïÑÎûò Ïó¨Î∞±ÏùÑ ÌôïÎ≥¥ÌïòÏó¨ Ïª®ÌÖêÏ∏† Í≤πÏπ® Î∞©ÏßÄ */
.navbar-container {
  width: 90%;
  max-width: 1400px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}


/* Î°úÍ≥† Ïä§ÌÉÄÏùº */
.logo {
  font-size: 1.8rem;
  font-weight: bold;
  color: #ffffff;
  text-decoration: none;
  transition: color 0.3s ease;
}

.logo:hover {
  color: #4a90e2; /* IntelliJ Ïä§ÌÉÄÏùº ÌååÎûÄÏÉâ */
}

/* ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Î©îÎâ¥ Ïä§ÌÉÄÏùº */
.nav-menu {
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav-menu li {
  display: inline-block;
}

.nav-menu li a {
  font-size: 1.2rem;
  color: #ffffff;
  text-decoration: none;
  padding: 10px 15px;
  transition: all 0.3s ease;
  border-radius: 5px;
}

.nav-menu li a:hover {
  background: rgba(255, 255, 255, 0.1);
  color: #4a90e2;
}
