/* Ï†ÑÏ≤¥ ÌéòÏù¥ÏßÄ Ï§ëÏïô Ï†ïÎ†¨ */
.stack-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #1a1a1a;
}

/* Í∏∞Ïà† Ïä§ÌÉù Ïª®ÌÖåÏù¥ÎÑà */
.stack-container {
  text-align: center;
  max-width: 700px;
  padding: 40px 20px;
  background-color: #292929; /* üî• Ïñ¥ÎëêÏö¥ Î∞∞Í≤Ω */
  color: #fff;
  border-radius: 10px;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
}

/* Ï†úÎ™© Ïä§ÌÉÄÏùº */
.stack-container h1 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: #4a90e2;
}

/* ÏÑ§Î™Ö Ïä§ÌÉÄÏùº */
.stack-description {
  font-size: 1.2rem;
  margin-bottom: 20px;
  color: #ccc;
}

/* Í∏∞Ïà† Ïä§ÌÉù Î¶¨Ïä§Ìä∏ */
.stack-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

/* Í∞úÎ≥Ñ Í∏∞Ïà† ÏïÑÏù¥ÌÖú */
.stack-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 120px;
  height: 120px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
  font-size: 1rem;
  font-weight: bold;
  color: #fff;
  transition: transform 0.3s ease-in-out, background 0.3s;
}

/* ÏïÑÏù¥ÏΩò Ïù¥ÎØ∏ÏßÄ */
.icon-img {
  width: 50px;
  height: 50px;
  margin-bottom: 10px;
}

/* Ìò∏Î≤Ñ Ìö®Í≥º */
.stack-item:hover {
  transform: scale(1.1);
  background: rgba(255, 255, 255, 0.2);
}
